import storage from 'good-storage'
export default {
  name: 'column-cell',
  props: {
    item: {
      type: Object,
      default () {
        return {
          _id: '59e7163129a1430b980ca41f',
          backgroundUrl: 'http://',
          classifyId: '59e7163129a1430b980ca41f',
          classifyName: '',
          createTime: 1508316721019,
          creator: 'objectid',
          imgUrl: 'http://placeholder.qiniudn.com/800x300/FFEF7D/ffffff',
          introduction: '',
          lastCourse: 'name',
          name: '',
          popular: true,
          subtitle: '',
          updateTime: 1508484702552
        }
      }
    }
  },
  data () {
    return {
      userId: ''
    }
  },
  computed: {
  },
  components: {},
  watch: {},
  methods: {
    updateTime (updateTime) {
      const update = (Date.now() - updateTime) / 1000 / 60 / 60
      if (update < 24 && update > 1) { // 1 - 24
        return (update | 0) + ''
      } else if (update > 24) { // > 24
        return (update / 24 | 0) + ''
      } else { // 1
        return ''
      }
    },
    goItem (columnId) {
      window.location.href = `/column.html#/?columnId=${columnId}`
    }
  },
  created () {
    this.userId = storage.get('aiwei').userInfo._id
  },
  mounted () {}
}



